body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
    height: 190px;
    width: 225px;
}

.card {
    margin: .85% 1.1% 1.1% 1%;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
}
.navbar {
    background-color: #8D5A97;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
}
.title {
    width: 75%;
}
ul {
    width: 100%;
}
li {
    display: inline-block;
    padding: 10px 0;
    width: 25%;
    text-align: center;
  }
.comms {
    width: 100%;
}
.render-score{
    width: 100%;
}
.render-high-score{
    width: 100%;
}
.btn {
    background-color: #907F9F;
}

body {
    background-image: repeating-linear-gradient(#B7FDFE, #5EF38C 10%, #FB8B24 15%);;
}
